<template>
  <div style="background: black;margin: 0;padding: 0;">
    <vue-particles
      color="#dedede"
      :particleOpacity="0.7"
      :particlesNumber="80"
      shapeType="circle"
      :particleSize="4"
      linesColor="#dedede"
      :linesWidth="1"
      :lineLinked="true"
      :lineOpacity="0.4"
      :linesDistance="150"
      :moveSpeed="3"
      :hoverEffect="true"
      hoverMode="grab"
      :clickEffect="true"
      clickMode="push">
    </vue-particles>

    <div style="position: absolute;top: 0;height: 100%;width: 70%;left: 15%">
      <el-container>
        <el-header style="display: flex">
          <div style="height: 100%;width: 20%;display: flex;align-items: center;justify-content: flex-start;">
            <span style="font-size: 24px">超简图床</span>
          </div>

          <div style="height: 100%;width: 80%;display: flex;align-items: center;justify-content: flex-end;">
            <div style="display: flex;height: 100%;align-items: flex-start;justify-content: center" v-if="loginState">
              <div class="menu-item" :class="{'active':activeIndex===0}" @click="menuItemClick(0)">
                <span>
                  上传
                </span>
              </div>
              <div class="menu-item" :class="{'active':activeIndex===1}" @click="getAllPic()">
                <span>
                  我的
                </span>
              </div>
              <div class="menu-item" :class="{'active':activeIndex===2}" @click="logout()">
                <span>
                  登出
                </span>
              </div>
            </div>
            <div style="display: flex;height: 100%;align-items: flex-start;justify-content: center" v-else>
              <div class="menu-item" :class="{'active':activeIndex===0}" @click="menuItemClick(0)">
                <span>
                  主页
                </span>
              </div>
              <div class="menu-item" :class="{'active':activeIndex===1}" @click="menuItemClick(1)">
                <span>
                  登录
                </span>
              </div>
              <div class="menu-item" :class="{'active':activeIndex===2}" @click="menuItemClick(2)">
                <span>
                  注册
                </span>
              </div>
            </div>
          </div>
        </el-header>

        <el-main>
          <div v-if="loginState">
            <div v-if="activeIndex === 0">
              <div
                style="margin-top: 20%;width: 100%;padding: 20px 0;display: flex;align-items: center;justify-content: center;flex-direction: column">
                <el-upload
                  ref="upload"
                  :multiple="false"
                  :limit="1"
                  :file-list="fileList"
                  action="http://localhost:60890/api"
                  :on-preview="handlePreview"
                  :on-remove="handleRemove"
                  :on-change="handleChange"
                  :with-credentials="true"
                  accept="image/jpg,image/jpeg,image/png"
                  :auto-upload="false">
                  <el-button style="background-color: white;color: black" slot="trigger" size="small" type="primary"
                             :disabled="fileList.length !== 0">选择图片
                  </el-button>
                  <el-button style="background-color: white;color: black;margin-left: 10px;" size="small" type="success"
                             @click="submitUpload" :disabled="fileList.length === 0">上传到服务器
                  </el-button>
                </el-upload>
              </div>
            </div>
            <div v-else-if="activeIndex === 1">
              <div style="width: 100%;display: flex">
                <div v-for="(item, i) in imageList" v-bind:key="i">
                  <el-image :src="item"></el-image>
                </div>
              </div>
            </div>
          </div>
          <div v-else>
            <div v-if="activeIndex === 0">
              <div
                style="padding-top: 20%;width: 100%;display: flex;align-items: center;justify-content: center;flex-direction: column">
                <div style="font-size: 36px">
                  <span>
                    轻量级图床程序
                  </span>
                </div>
                <div style="font-size: 21px;margin-top: 10px;">
                  <span>
                    本图床提供简单便捷的服务器存储(使用前请先注册)
                  </span>
                </div>
                <div
                  style="margin-top: 20px;padding: 10px 15px;background-color: white;border-radius: 6px;cursor: pointer"
                  @click="jumpToCode">
                  <span style="color: black;">
                    获取源码
                  </span>
                </div>
              </div>
            </div>
            <div v-else-if="activeIndex === 1">
              <div
                style="padding-top: 20%;width: 100%;display: flex;align-items: center;justify-content: center;flex-direction: column">
                <div style="width: 40%;margin-bottom: 30px;display: flex;align-items: center;">
                  <div style="height: 100%;width: 20%;display: flex;justify-content: center;align-items: center;">
                    <span>用户名</span>
                  </div>
                  <el-input style="width: 80%" v-model="username" placeholder="用户名"></el-input>
                </div>
                <div style="width: 40%;margin-bottom: 30px;display: flex;align-items: center">
                  <div style="height: 100%;width: 20%;display: flex;justify-content: center;align-items: center;">
                    <span>密码</span>
                  </div>
                  <el-input style="width: 80%" v-model="password" placeholder="密码" show-password></el-input>
                </div>
                <div style="width: 40%;display: flex;justify-content: center;align-items: center">
                  <div
                    style="width: 20%;padding: 10px 15px;background-color: white;border-radius: 6px;cursor: pointer"
                    @click="login">
                    <span style="color: black;">
                      登录
                    </span>
                  </div>
                </div>
              </div>
            </div>
            <div v-else-if="activeIndex === 2">
              <div
                style="padding-top: 20%;width: 100%;display: flex;align-items: center;justify-content: center;flex-direction: column">
                <div style="width: 40%;margin-bottom: 30px;display: flex;align-items: center;">
                  <div style="height: 100%;width: 20%;display: flex;justify-content: center;align-items: center;">
                    <span>用户名</span>
                  </div>
                  <el-input style="width: 80%" v-model="username" placeholder="用户名"></el-input>
                </div>
                <div style="width: 40%;margin-bottom: 30px;display: flex;align-items: center">
                  <div style="height: 100%;width: 20%;display: flex;justify-content: center;align-items: center;">
                    <span>密码</span>
                  </div>
                  <el-input style="width: 80%" v-model="password" placeholder="密码" show-password></el-input>
                </div>
                <div style="width: 40%;display: flex;justify-content: center;align-items: center">
                  <div
                    style="width: 20%;padding: 10px 15px;background-color: white;border-radius: 6px;cursor: pointer"
                    @click="signUp">
                    <span style="color: black;">
                      注册
                    </span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </el-main>
      </el-container>
    </div>
  </div>
</template>

<script>
  export default {
    name: "Index",
    data() {
      return {
        loginState: false,
        activeIndex: 0,
        username: "",
        password: "",
        filename: "",
        fileList: [],
        imageList: [],
      }
    },
    created() {
    },
    mounted() {
    },
    methods: {
      menuItemClick(index) {
        this.activeIndex = index
        this.password = ""
        this.username = ""
      },
      jumpToCode() {
        window.open("https://github.com/MrWang98/imgbed", "_blank")
      },
      login() {
        this.$ajax.post(
          "http://localhost:60890/login",
          this.$qs.stringify({
            'username': this.username,
            'password': this.password,
            'type': 2,
          }),
          {
            headers: {
              'Content-Type': 'application/x-www-form-urlencoded',
            }
          }
        ).then(
          (res) => {
            if (res.data.code === 1) {
              alert("登录成功")
              this.password = ""
              this.loginState = true
              this.activeIndex = 0
            } else {
              alert("登陆失败")
              this.password = ""
            }
          }
        ).catch(
          (res) => {
            console.log(res)
            alert("网络错误")
          }
        )
      },
      signUp() {
        this.$ajax.post(
          "http://localhost:60890/signUp",
          this.$qs.stringify({
            'username': this.username,
            'password': this.password
          }),
          {
            headers: {
              'Content-Type': 'application/x-www-form-urlencoded',
            }
          }
        ).then(
          (res) => {
            if (res.data.code === 1) {
              alert("注册成功")
              this.password = ""
            } else {
              alert("注册失败")
              this.password = ""
            }
          }
        ).catch(
          (res) => {
            console.log(res)
            alert("网络错误")
          }
        )
      },
      logout() {
        this.$ajax.post(
          "http://localhost:60890/logout",
          "",
          {},
        ).then(
          (res) => {
            if (res.data.code === 1) {
              alert("登出成功")
              this.username = ""
              this.loginState = false
            } else {
              alert("登出失败")
            }
          }
        ).catch(
          (res) => {
            console.log(res)
            alert("网络错误")
          }
        )
      },
      submitUpload() {
        let file = document.getElementsByClassName('el-upload__input')[0].files[0]
        // eslint-disable-next-line no-unused-vars
        let baseData = "";
        let r = new FileReader();
        r.onload = () => {
          baseData = r.result
          console.log(this.fileList)
          this.$ajax.post(
            "http://localhost:60890/api",
            this.$qs.stringify({
              'key': encodeURI(this.fileList[0].name),
              'imgBase64': encodeURIComponent(baseData.split(',')[1])
            }),
            {
              headers: {
                'Content-Type': 'application/json;charset=UTF-8'
              }
            }
          ).then(
            (res) => {
              console.log(res)
            }
          )
        }
        r.readAsDataURL(file)
      },
      handleRemove(file, fileList) {
        console.log(file, fileList)
      },
      handlePreview(file) {
        console.log(file)
      },
      handleChange(file, fileList) {
        console.log(file, fileList)
        this.fileList = fileList
      },
      getAllPic() {
        this.menuItemClick(1)
        if (this.imageList.length === 0) {
          this.$ajax.get(
            "http://localhost:60890/mine"
          ).then(
            (res) => {
              console.log(res)
              if(res.data.code === 1){
                this.imageList = res.data.pics
              } else {
                alert("网络错误")
              }
            }
          )
        }
      }
    }
  }
</script>

<style scoped>
  * {
    color: white;
  }

  .menu-item {
    display: flex;
    height: 100%;
    padding: 0 20px;
    align-items: center;
    justify-content: center;
    cursor: pointer;
  }

  .menu-item:hover {
    background-color: rgba(255, 255, 255, 0.2);
  }

  .active {
    background-color: rgba(255, 255, 255, 0.2);
  }

  .el-upload-list--text {
    color: white;
  }
</style>
